import { AccountNumberType } from '@backbase/ui-ang/account-number-pipe';

/**
 * Account info property type
 */
export enum AccountInfoPropertyType {
  ACCOUNT_NUMBER = 'ACCOUNT_NUMBER',
  ACCOUNT_STATE = 'ACCOUNT_STATE',
  AMOUNT = 'AMOUNT',
  DATE = 'DATE',
  DEBIT_CARDS = 'DEBIT_CARDS',
  FREQUENCY = 'FREQUENCY',
  NUMBER = 'NUMBER',
  PERCENT = 'PERCENT',
  RENEWAL_STATUS = 'RENEWAL_STATUS',
  TEXT = 'TEXT',
  UPDATE_TIME = 'UPDATE_TIME',
}

/**
 * Account info property configuration
 */
export interface AccountInfoPropertyBase {
  type: AccountInfoPropertyType;
  key: string;
  label?: string;
  tooltip?: string;
}

/**
 * Account info property for account numbers
 */
export interface AccountInfoPropertyAccountNumber extends AccountInfoPropertyBase {
  type: AccountInfoPropertyType.ACCOUNT_NUMBER;
  numberType: AccountNumberType;
  hideMaskIndicator?: boolean;
}

/**
 * Account info property for account state
 */
export interface AccountInfoPropertyAccountState extends AccountInfoPropertyBase {
  type: AccountInfoPropertyType.ACCOUNT_STATE;
  showIcon?: boolean;
}

/**
 * Account info property for amounts
 */
export interface AccountInfoPropertyAmount extends AccountInfoPropertyBase {
  type: AccountInfoPropertyType.AMOUNT;
  currency?: string;
  currencyFormat?: 'code' | 'symbol';
}

/**
 * Account info property for dates
 */
export interface AccountInfoPropertyDate extends AccountInfoPropertyBase {
  type: AccountInfoPropertyType.DATE;
  dateFormat?: string;
}

/**
 * Account info property for debit cards
 */
export interface AccountInfoPropertyDebitCards extends AccountInfoPropertyBase {
  type: AccountInfoPropertyType.DEBIT_CARDS;
}

/**
 * Account info property for frequency
 */
export interface AccountInfoPropertyFrequency extends AccountInfoPropertyBase {
  type: AccountInfoPropertyType.FREQUENCY;
  unitKey: string;
}

/**
 * Account info property for numbers
 */
export interface AccountInfoPropertyNumber extends AccountInfoPropertyBase {
  type: AccountInfoPropertyType.NUMBER;
  decimalPlaces?: number;
}

/**
 * Account info property for percents
 */
export interface AccountInfoPropertyPercent extends AccountInfoPropertyBase {
  type: AccountInfoPropertyType.PERCENT;
}

/**
 * Account info property for a renewal status
 */
export interface AccountInfoPropertyRenewalStatus extends AccountInfoPropertyBase {
  type: AccountInfoPropertyType.RENEWAL_STATUS;
}

/**
 * Account info property for texts
 */
export interface AccountInfoPropertyText extends AccountInfoPropertyBase {
  type: AccountInfoPropertyType.TEXT;
}

/**
 * Account info property for updated time
 */
export interface AccountInfoPropertyUpdateTime extends AccountInfoPropertyBase {
  type: AccountInfoPropertyType.UPDATE_TIME;
  dateFormat?: string;
}

/**
 * Account info property
 */
export type AccountInfoProperty =
  | AccountInfoPropertyAccountNumber
  | AccountInfoPropertyAccountState
  | AccountInfoPropertyAmount
  | AccountInfoPropertyDate
  | AccountInfoPropertyDebitCards
  | AccountInfoPropertyFrequency
  | AccountInfoPropertyNumber
  | AccountInfoPropertyPercent
  | AccountInfoPropertyRenewalStatus
  | AccountInfoPropertyText
  | AccountInfoPropertyUpdateTime;
